h1 {
  text-align: center;
}

body {
  overflow-x: hidden;
}

.status{
  position: fixed;
  top: 0;
  left: 0;
  margin-left: 1%;
  color: red;
}

.announcement{
  text-align: left;
}
#announcement-container {
  gap: 10px;
  border: steelblue 1px solid;
  width: 100%;
  max-height: 85vh;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  padding-left: 10px;
}

.header {
  align-content: center;
  display: flex;
  gap: 10px;
}

.timestamp, .author {
  margin: 0;
  display: inline-block;
}

.timestamp {
  color: dimgray;
}

.image {
  max-height: 500px;
  max-width: 500px;
  border-radius: 2%;
  margin-top: 5px;
}
